.react-google-flight-datepicker .icon-calendar {
    width: 24px;
    height: 24px;
    fill: #ff2260;
    margin-right: 12px
}

.react-google-flight-datepicker .date:focus::after,
.react-google-flight-datepicker .date.is-focus::after {
    content: '';
    margin: -1px;
    border: 2px solid #ff2260;
    border-radius: 4px;
    position: absolute;
    pointer-events: none;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 5
}